import icons from 'url:../../img/icons.svg';

export default class View {
  _data;

  render(data, render = true) {
	if(!data || (Array.isArray(data) && data.length === 0))
		return this.renderError();

    this._data = data;
    const markup = this._generateMarkup();

		if(!render) return markup;

    this._clear();
    this._parentElement.insertAdjacentHTML('afterbegin', markup);
	}

	update(data){
    this._data = data;
    const newMarkup = this._generateMarkup();
		//We are gonna compare the newMarkup (which is a string) to the DOM elements
		//To fix this, convert the newMarkup-string to an DOM-object
		const newDOM = document.createRange().createContextualFragment(newMarkup);
																					//this method converts string in to real DOM node objects
		const newElements = Array.from(newDOM.querySelectorAll('*')); //Returns an array from a node list
		const currElements = Array.from(this._parentElement.querySelectorAll('*')); //the same as the prev

		newElements.forEach((newEl, i) => { //element, index
			const curEl = currElements[i];
			// console.log(curEl, newEl.isEqualNode(curEl));

			//Update changed TEXT
			if(!newEl.isEqualNode(curEl) && newEl.firstChild?.nodeValue.trim() !== ''){
				// console.log('🥞', newEl.firstChild.nodeValue.trim());
				curEl.textContent = newEl.textContent;
			}
			//Update changed ATTRIBUTES
			if(!newEl.isEqualNode(curEl)){
				// console.log(Array.from(newEl.attributes));
				Array.from(newEl.attributes).forEach(attr => curEl.setAttribute(attr.name, attr.value))
			}
		})
	}

  _clear() {
    this._parentElement.innerHTML = '';
	}

	renderSpinner(){
		const markup = `
				<div class="spinner">
				<svg>
					<use href="${icons}#icon-loader"></use>
				</svg>
			</div>
		`;
		this._clear();
		this._parentElement.insertAdjacentHTML('afterbegin', markup);
	}

	renderError(message = this._errorMessage) {
		const markup = `
			<div class="error">
				<div>
					<svg>
						<use href="${icons}#icon-alert-triangle"></use>
					</svg>
				</div>
				<p>${message}</p>
			</div>
		`;
		this._clear();
		this._parentElement.insertAdjacentHTML('afterbegin', markup);
	}

	renderMessage(message = this._message) {
		const markup = `
			<div class="message">
				<div>
					<svg>
						<use href="${icons}#icon-smile"></use>
					</svg>
				</div>
				<p>${message}</p>
			</div>
		`;
		this._clear();
		this._parentElement.insertAdjacentHTML('afterbegin', markup);
	}
}